/* feature */
.feature-list {
    width: calc(100% - 20px);
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: auto;
}

.feature-item-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.feature-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    border-radius: var(--border-radius);
}

.feature-head {
    width: calc(100% - 20px);
    display: flex;
    padding: 10px;
}

.feature-head-left {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.feature-head-left-title {
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.feature-head-right {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: right;
    gap: 5px;
}

.feature-head-right-btn {
    font-size: var(--font-size-mid);
    font-weight: bold;
    color: var(--primary-color);
    cursor: pointer;
}

.feature-content {
    width: calc(100% - 20px);
    padding: 10px;
}

/* chat-list */
.chat-record {
    position: fixed;
    bottom: 90px;
    left: 20px;
    right: 20px;
}

.chat-record-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    gap: 10px;
}

.chat-record-list-item {
    flex-shrink: 0;
    height: 50px;
    color: white;
    padding-bottom: 10px;
}

.chat-record-list-content {
    height: 100%;
    padding: 0 10px;
    background: var(--primary-color);
    border-radius: var(--border-radius);
    font-weight: bold;
    cursor: pointer;
}

.chat-record-list-content-del {
    color: red;
    margin-left: 10px;
}

/* habbit */
.habit-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    gap: 10px;
}

.habit-item {
    position: relative;
    flex-shrink: 0;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 10px;
}

.habit-item-ok {
    position: absolute;
    top: 0;
    right: 25%;
    font-size: var(--font-size-big);
    text-align: center;
    line-height: 20px;
    color: var(--success-color);
    font-weight: bold;
    /* animation: animate-popDown .3s linear; */
}

.habit-item-icon {
    font-size: 50px;
    cursor: pointer;
}

.habit-item-time {
    font-size: var(--font-size-mini);
    color: var(--primary-color);
    font-weight: bold;
}

.habit-item-title {
    font-size: var(--font-size-small);
    font-weight: bold;
}

.habit-item-stat {
    font-size: var(--font-size-mini);
}

.todayTodo-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
}

.todayTodo-item {
    font-size: var(--font-size-mid);
    display: flex;
    flex-direction: row;
}

.todayTodo-item-info {
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    padding: 10px;
    font-weight: bold;
    color: var(--primary-color);
    white-space: nowrap;
}

.todayTodo-item-title {
    height: 100%;
    font-size: var(--font-size-mid);
}

.todayTodo-item-operate {
    width: 100%;
    height: 100%;
    margin-left: 10px;
    color: var(--main-text-color-2);
    font-size: var(--font-size-big);
    cursor: pointer;
}

.todayTodo-item-operate.ok {
    color: var(--primary-color);
}

.todayTodo-item-icon {
    height: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;
}

.timer-list {
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow-x: auto;
}

.timer-list-item {
    font-size: var(--font-size-mid);
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: row;
    padding: 10px;
    color: var(--warn-color);
    font-weight: bold;
    white-space: nowrap;
}

.timer-list-item-title {
    height: 100%;
}

.timer-list-item-time {
    height: 100%;
}

/* env */
.env-location {
    margin-right: 10px;
    font-size: var(--font-size-mid);
    font-weight: bold;
    color: var(--primary-color);
}

.env-temp-text {
    color: var(--main-text-color-2);
}

.env-date {
    display: flex;
    flex-direction: column;
}

.env-time {
    text-align: center;
}

.env-time-info {
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
}

.env-time {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
    font-size: 50px;
}

.env-day {
    width: 50%;
    height: 100%;
    color: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: right;
    font-size: 30px;
}

/* event */
.event-item-list {
    display: flex;
    flex-direction: row;
    overflow: auto;
    gap: 10px;
}

.event-item {
    flex-shrink: 0;
    width: 250px;
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    padding: 10px;
    margin-bottom: 10px;
}

.event-item-head {
    display: flex;
}

.event-type {
    width: 50%;
    justify-content: left;
    font-size: var(--font-size-big);
}

.event-time-info {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: right;
    color: var(--main-text-color-2);
}

.event-date {
    display: flex;
    align-items: center;
    justify-content: right;
}

.event-time {
    display: flex;
    align-items: center;
    justify-content: right;
}

.event-item-content {
    display: flex;
    flex-direction: row;
    height: 100px;
}

.event-title {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--font-size-big);
}

.event-left-info {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--primary-color);
    font-size: var(--font-size-mid);
}

.event-left-day {
    font-size: var(--font-size-mid);
    font-weight: bold;
    text-align: center;
}

.note-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
}

.note-item {
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.note-info {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    max-width: 300px;
    padding: 10px;
    border-radius: var(--border-radius);
    background: var(--main-bg-color-1);
    cursor: pointer;
}

.note-title {
    width: 100%;
    font-size: var(--font-size-big);
    font-weight: bold;
    color: var(--primary-color);
}

.note-time-type {
    width: 100%;
    font-size: var(--font-size-small);
    color: var(--main-text-color-2);
    white-space: nowrap;
}

.note-view {
    position: fixed;
    left: 10px;
    right: 10px;
    top: 50px;
    z-index: var(--max-zindex);
    padding: 10px;
    background: var(--main-bg-color-2);
    border: 5px solid var(--primary-color);
    border-radius: var(--border-radius);
}

.note-view-title {
    height: 30px;
    width: 100%;
    margin-bottom: 10px;
    font-size: var(--font-size-big);
    font-weight: bold;
    color: var(--primary-color);
}

.note-view-title input {
    height: 100%;
    width: 100%;
    padding: 0;
    border: none;
    outline: none;
    background: var(--main-bg-color-2);
    font-size: var(--font-size-big);
    color: var(--primary-color);
    border-radius: 0;
}

.note-view-time-type {
    height: 20px;
    width: 100%;
    margin-bottom: 10px;
    font-size: var(--font-size-small);
    color: var(--main-text-color-2);
    text-align: right;
}

.note-view-content {
    max-height: calc(100vh - 120px - 50px - 120px - 10px - 10px);
    width: 100%;
    margin-bottom: 10px;
    overflow: auto;
    word-break: break-all;
    font-size: var(--font-size-mid);
}

.note-view-content textarea {
    width: calc(100% - 22px);
    height: calc(100vh - 120px - 50px - 120px - 10px - 10px - 22px - 2px);
    padding: 10px;
    background: var(--main-bg-color-2);
    resize: none;
    font-size: var(--font-size-mid);
    color: var(--main-text-color-1);
    outline: none;
}

.note-view-content textarea:focus {
    border: 1px solid var(--primary-color);
}


.note-view-operate {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.note-view-operate-btn {
    flex-shrink: 0;
    width: 33.3%;
    font-size: var(--font-size-big);
    cursor: pointer;
}

/*news*/
.news-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow: auto;
}

.news-item {
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    padding: 10px;
}

.news-item-time {
    font-size: var(--font-size-small);
    color: var(--warn-color);
}

.news-item-title {
    font-size: var(--font-size-big);
    font-weight: bold;
    color: var(--primary-color);
    max-width: 200px;
    white-space: nowrap;
    overflow: auto;
}