.assistant-index {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.assistant-index-head {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    width: calc(100% - 40px);
    z-index: var(--max-zindex);
}

.assistant-index-main {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    padding-top: 50px;
    padding-bottom: 100px;
}

.assistant-index-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 90px;
    z-index: var(--max-zindex);
}

/*assistant*/
.assistant {
    width: calc(100% - 60px);
    height: calc(100% - 60px);
    padding: 30px;
}

.assistant img {
    height: 100%;
}

.assistant-talk {
    position: fixed;
    top: 18vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
}

.assistant-data-center {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    backdrop-filter: var(--backdrop-filter);
    padding-top: 50px;
    overflow: auto;
}

.assistant-talk-div {
    position: relative;
    flex-shrink: 0;
    background: var(--main-glass-color);
    background: var(--main-bg-color-2);
    backdrop-filter: var(--backdrop-filter);
    max-width: calc(100% - 30px - 20px - 20px);
    max-height: 50vh;
    border: 5px solid var(--primary-color);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: column;
    padding: 10px;
    font-weight: bold;
    overflow: auto;
}

.assistant-talk-title {
    width: 100%;
    height: 30px;
    font-size: var(--font-size-mid);
    color: var(--primary-color);
    font-weight: bold;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
}

.assistant-talk-content {
    flex-shrink: 0;
    width: 100%;
    height: calc(100% - 30px);
    font-size: var(--font-size-mid);
    word-break: break-all;
    overflow: auto;
    cursor: pointer;
}

.assistant-talk-operate {
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.assistant-talk-operate-btn {
    height: 80%;
    font-size: var(--font-size-mid);
    padding: 0 10px;
    border-radius: var(--border-radius);
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--primary-color);
    color: white;
}

/*head-logo*/
.assistant-head-logo {
    height: 40px;
    width: 40px;
    z-index: var(--max-zindex);
}

.assistant-head-logo img {
    height: 100%;
    width: 100%;
}

/*head-notification*/
.assistant-head-notification {
    height: 40px;
    max-width: calc(100% - 50px);
    margin-left: 10px;
}

.assistant-head-notification-item {
    background: var(--primary-color);
    padding: 10px;
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    border-radius: var(--border-radius);
    font-weight: bold;
    white-space: nowrap;
    overflow-y: hidden;
    color: white;
}

/*answer*/
.assistant-answer-mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: var(--mid-zindex);
    background: var(--main-glass-color);
}

.assistant-answer {
    position: fixed;
    top: 50px;
    left: 10px;
    right: 10px;
    background: var(--main-bg-color-1);
    border-radius: var(--border-radius);
    padding: 10px;
    z-index: var(--max-zindex);
    box-shadow: var(--primary-box-shadow);
}

.assistant-answer-title {
    height: 33px;
    width: 100%;
    font-size: var(--font-size-mid);
    font-weight: bold;
    white-space: nowrap;
    overflow-x: auto;
    color: var(--primary-color);
}

.assistant-answer-content {
    max-height: calc(80vh - 33px - 50px - 120px);
    width: 100%;
    overflow: auto;
    word-break: break-all;
    font-size: var(--font-size-mid);
    /*color: white;*/
}

/*main-notification*/
.main-notification {
    position: fixed;
    top: 50px;
    bottom: 100px;
    left: 10px;
    right: 10px;
    background: var(--main-bg-color-2);
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    padding: 10px;
    z-index: var(--max-zindex);
}

.main-notification-info {
    padding-top: 30px;
    height: calc(100% - 30px - 130px);
    text-align: center;
    width: 100%;
    font-size: 50px;
    font-weight: bold;
    color: var(--primary-color);
    overflow-y: auto;
}

.main-notification-time {
    height: 80px;
    line-height: 80px;
    text-align: center;
    width: 100%;
    font-size: 30px;
    font-weight: bold;
    color: var(--warn-color);
}

.main-notification-operate {
    height: 50px;
    line-height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.operate-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-big);
    font-weight: bold;
    width: 50%;
}

.data-center-enter {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: var(--font-size-big);
    color: var(--primary-color);
    z-index: var(--max-zindex);
    cursor: pointer;
}

.menu-bar {
    width: 100%;
    height: 100%;
    display: flex;
    color: var(--main-text-color-2);
}

.menu-item {
    width: 50%;
    height: 100%;
    cursor: pointer;
    font-size: var(--font-size-mid);
}

.timeCount {
    width: 100%;
}

.timeCount.big {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    backdrop-filter: var(--backdrop-filter);
    font-size: 50px;
}