.text-input {
    height: calc(100% - 25px);
    width: calc(100% - 40px);
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
}

.text-send-input {
    width: 100%;
    height: 100%;
    border-radius: 30px;
    box-shadow: var(--primary-box-shadow);
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter);
}

.text-left-btn {
    width: 30px;
    height: 100%;
    padding-left: 15px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    background: rgba(0, 0, 0, 0);
    color: var(--primary-color);
    font-size: var(--font-size-big);
    font-weight: bold;
    cursor: pointer;
}

.text-left-btn-empty {
    color: var(--main-text-color-2);
}

.text-send-input input {
    width: calc(100% - 45px - 45px - 20px);
    height: 100%;
    padding: 0 10px;
    border: none;
    outline: none;
    background: rgba(0, 0, 0, 0);
    color: var(--main-text-color-1);
    font-size: var(--font-size-mid);
    border: 0;
    border-radius: 0;
}

.text-right-btn {
    width: 30px;
    height: 100%;
    padding-right: 15px;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    background: rgba(0, 0, 0, 0);
    color: var(--main-text-color-2);
    font-size: var(--font-size-big);
    font-weight: bold;
    cursor: pointer;
}

.text-right-btn.ok {
    color: var(--primary-color);
}